<template>
  <div
    :class="[$style['form-group'], disabled ? $style['disabled'] : '']"
  >
    <label
      :for="id"
      :class="$style.label"
    >
    {{label}}
    <span
      v-if="!required"
    > (Optional)</span>
    </label>
    <div
      :id="description"
      :class="$style['help-text']"
      v-show="description"
    >
    <!-- @slot Use this to include helper text/markup. -->
      <slot></slot>
    </div>
    <input
      :id="id"
      type="text"
      :class="$style['form-control']"
      :value="value"
      :disabled="disabled"
      :placeholder="placeholder"
      v-bind="$attrs"
      :name="name"
      :aria-describedby="description"
      @input="$emit('input', $event.target.value)"
    >
  </div>
</template>

<script>
import { v4 as uuid } from "uuid"
/**
   * A base text input that is styled according to the Design Library.
   * Automatically assigns unique ids to use for the label and
   * description text to use recommended html structure.
   */
export default {
  status: "prototype",
  name: "BaseInput",
  props: {
    /** ID value to use for main input */
    id: {
      type: String,
      default: () => ("input" + uuid())
    },
    /** ID value to use for input help text*/
    helpId: {
      type: String,
      default: () => ("input" + uuid())
    },
    /** The text value for the input. */
    value: String,
    /** Should the input be disabled? */
    disabled: {
      type: Boolean,
      default: false
    },
    /** The placeholder for the input. */
    placeholder: String,
    /** The label text for the input. */
    label: {
      type: String,
      default: ""
    },
    name: {
      type: String
    },
    required: {
      type: Boolean,
      default: false
    },
  },
  computed: {
    description(){
      return this.$slots.default ? this.helpId : false
    }
  }
}
</script>

<style lang="scss" module>

  .required {
    color: $color-scarlet;
  }
  .label {
    @include stack-space($space-xs);
    font-family: $font-heading;
    font-weight: $weight-bold;
    display: inline-block;
  }

  .form-control {
    @include inset-squish-space($space-s);
    display: block;
    width: 100%;

    font-size: $size-m;
    font-weight: $weight-normal;
    line-height: $line-height-m;
    color: $color-gray-darker;
    background-color: $color-white;
    background-clip: padding-box;
    border: 2px solid $color-gray-light;
    border-bottom: 2px solid $color-gray-dark;
    border-radius: 0;
    transition: border-color $duration-quickly ease-in-out, box-shadow $duration-quickly ease-in-out;
    caret-color: $color-scarlet;

    &:focus {
      box-shadow: none;
      outline: none;
      border-bottom: 2px solid $color-scarlet;
    }

  }

  .help-text {
    font-family: $font-heading;
    line-height: $line-height-s;
    color: $color-gray-dark;
    font-style: italic;
    @include stack-space($space-s);
  }

.form-group {
  @include stack-space($space-m);
  break-inside: avoid;

  &.disabled {
    border-color: $color-gray-disabled;
    color: $color-gray-light;
    .form-control {
      border-color: $color-gray-disabled;
      color: $color-gray-light;
    }
  }
}
</style>

<docs>
```jsx
const value = ""
  <BaseInput
    label="Default Input"
    id="my-input"
    help-id="my-helper"
    v-model="value"
  >
  Help text goes here.
  </BaseInput>
```

