<template>

<nav role="navigation" aria-label="Pagination" :class="outerClass">
	<ul
		class="bux-pagination__list">
			<li
        v-if="!isFirst"
        class="bux-pagination__item"
      >
				<a
          class="bux-pagination__link bux-pagination__link--previous"
          href="#"
          aria-label="Go to previous page"
          @click.prevent="goPrev"
        ></a>
			</li>

			<li
        v-if="!isFirst"
        class="bux-pagination__item"
      >
				<a
          class="bux-pagination__link"
          href="#"
          aria-label="Go to page 1"
          @click.prevent="goToPage(1)"
        >1</a>
			</li>

			<li
        class="bux-pagination__item"
        v-if="truncateStart"
      >
				<span class="bux-pagination__link bux-pagination__link--truncated" aria-hidden="true"></span>
			</li>
					<li
            v-for="pagenum in previousPages"
            :key="pagenum"
            class="bux-pagination__item"
          >
						<a
              class="bux-pagination__link"
              href="#"
              :aria-label="`Go to page ${ pagenum }`"
              @click.prevent="goToPage(pagenum)"
            >
							{{ pagenum }}
						</a>
					</li>
		<li class="bux-pagination__item">
			<a
        class="bux-pagination__link bux-pagination__link--current"
        href="#"
        @click.prevent
        :aria-label="`Current page, page ${ currentPage }`" aria-current="true">
				{{ currentPage }}
			</a>
		</li>

					<li
            v-for="pagenum in nextPages"
            :key="pagenum"
            class="bux-pagination__item"
          >
						<a
              class="bux-pagination__link"
              href="#"
              @click.prevent="goToPage(pagenum)"
              :aria-label="`Go to page ${ pagenum }`"
            >
							{{ pagenum }}
						</a>
					</li>
			<li
        v-if="truncateEnd"
        class="bux-pagination__item"
      >
				<span class="bux-pagination__link bux-pagination__link--truncated" aria-hidden="true"></span>
			</li>
			<li
        class="bux-pagination__item"
        v-if="currentPage < length"
      >
				<a
          class="bux-pagination__link"
          href="#"
          :aria-label="`Go to page ${length}`"
          @click.prevent="goToPage(length)"
        >{{length}}</a>
			</li>
			<li
        class="bux-pagination__item"
        v-if="currentPage != length"
      >
				<a
          class="bux-pagination__link bux-pagination__link--next"
          href="#"
          aria-label="Go to next page"
          @click.prevent="goNext"
        ></a>
			</li>
	</ul>
</nav>
</template>

<script>
  export default {
    model: {
      prop: 'currentPage',
      event: 'change'
    },
    props: {
      variant: {
        type: String,
        default: 'default',
        validator(variant) {
          return ['default', 'alt', 'reverse', 'truncated'].includes(variant)
        }
      },
      maxAfter: {
        type: Number,
        default: 2
      },
      maxBefore: {
        type: Number,
        default: 2
      },
      length: {
        type: Number,
        default: 1
      },
      currentPage: {
        type: Number,
        default: 1
      }
    },
    computed: {
      outerClass() {
        const classes = ['bux-pagination']
        if (this.variant) {
          classes.push(`bux-pagination--${this.variant}`)
        }
        return classes
      },
      isFirst() {
        return this.currentPage === 1
      },
      previousPages() {
        if(this.currentPage < 3) return []
        const maxLength = this.maxBefore
        const neededLength = this.currentPage - 2 > 0 ? this.currentPage - 2 : 0
        const length = neededLength > maxLength ? maxLength : neededLength
        return Array.from({length}, (val, i) =>  this.currentPage - (length - i) )

/*
  {% set pages_previous = range(2, (current_page - 1)) %}
{% set pages_next = range((current_page + 1), (total_pages - 1)) %}
		{% for page in pages_previous %}
			{% set pagenum = (loop.index + 1) %}
			{% if pagenum < current_page %}
				{% if page not in truncate_pages %}
        */
      },
      nextPages() {
        const maxLength = this.maxAfter
        const length = this.length - ( this.currentPage + 1) > maxLength ? maxLength : this.length - ( this.currentPage + 1)

        return Array.from({length}, (val, i) => i + this.currentPage + 1)
      },
      truncateStart() {
        return this.previousPages[0] > 2
      },
      truncateEnd() {
        return (this.currentPage < this.length - 1) && this.length !== this.nextPages[this.nextPages.length - 1] + 1
      }
    },
    methods: {
      goPrev(){
        this.goToPage(this.currentPage - 1)
      },
      goNext(){
        this.goToPage(this.currentPage + 1)
      },
      goToPage(num) {
        this.$emit('change', num)
      }
    }
  }
</script>

<style lang="scss" scoped>

// import the required BUX base sass
    @import '../../assets/buxBase.scss';

// import the component specific sass
    @import '@osu/bux-source/src/components/pagination/_pagination.scss';
</style>

